	STATISTIC_ITEMS,
	TICKET_ACCEPT,
	TICKET_OPEN,
	TICKET_PAUSE,
	TICKET_INVALID,
	TICKET_CLOSE,
	TICKET_CLOSE_NORMAL,
	TICKET_CLOSE_ABNORMAL,
	TICKET_CLOSE_ON_TIME,
	TICKET_REPLY_OVER_48,
	COL_RESPONSE_ALARM_LEVEL,
