// 统计周期前需处理
export const statisticBeforePeriodNeedHandleResult = (array, allItems) => {
	let dataMap = {};
	for (let name of allItems) {
		dataMap[name] = initStatisticItem();
	}
	for (let result of array) {
		for (let name in result) {
			if (dataMap[name] == null) {
				dataMap[name] = initStatisticItem();
			}
			for (let category in dataMap[name]) {
				for (let item of STATISTIC_ITEMS) {
					if (item === TICKET_CLOSE_PROCESS_TIME) {
						let ticketCloseCount = result[name][category][TICKET_CLOSE];
						let ticketCloseProcessTime = result[name][category][TICKET_CLOSE_PROCESS_TIME];
						dataMap[name][category][TICKET_CLOSE_PROCESS_TIME] +=
							ticketCloseCount * ticketCloseProcessTime;
					} else if (item === TICKET_OPEN_PROCESS_TIME) {
						let ticketOpenProcessTime = result[name][category][TICKET_OPEN_PROCESS_TIME];
						let ticketOpenCount = result[name][category][TICKET_OPEN];
						dataMap[name][category][TICKET_OPEN_PROCESS_TIME] +=
							ticketOpenCount * ticketOpenProcessTime;
					}
					else {
						dataMap[name][category][item] +=
							result[name][category][item];
					}
				}
			}
		}
	}
	// get average process time
	for (let name in dataMap) {
		for (let category in dataMap[name]) {
			let ticketCloseProcessTime = dataMap[name][category][TICKET_CLOSE_PROCESS_TIME];
			let ticketCloseCount = dataMap[name][category][TICKET_CLOSE];
			dataMap[name][category][TICKET_CLOSE_PROCESS_TIME] =
				ticketCloseCount > 0 ? Number(ticketCloseProcessTime / ticketCloseCount).toFixed(2) : 0;

			let ticketOpenProcessTime = dataMap[name][category][TICKET_OPEN_PROCESS_TIME];
			let ticketOpenCount = dataMap[name][category][TICKET_OPEN];
			dataMap[name][category][TICKET_OPEN_PROCESS_TIME] =
				ticketOpenCount > 0 ? Number(ticketOpenProcessTime / ticketOpenCount).toFixed(2) : 0;
		}
	}
	return dataMap;
}

